@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
body,
html {
  margin: 0;
  padding: 0;
}



h1 {
  font-family: 'Noto Sans', sans-serif;
}

h3 {
  font-family: 'Lato', sans-serf;
  font-weight: 700;
}

.resume-list-header {
  font-weight: bold;
  font-size: 2em;
  text-decoration: underline;
  color: #2b2b2b;
  -webkit-text-decoration-color: #2b2b2b;
          text-decoration-color: #2b2b2b;
  text-decoration-thickness: 3px;
  font-family: 'Source Sans Pro', sans-serif;
}

hr {
  background-color: #fafbff;
  margin: 0;
  padding: 0;
}


.hidden {
  display: none !important;
}



.resume-container {
  min-height: 500px;
  padding: 25px 0;
  background-color: #fafbff;
  margin: 0;
}

.active {
  color: #cc5200;
}

.active:hover {
  color: #cc5200;
  text-decoration: none;
}

.nav-item {
  cursor: default;
  margin: 0 10px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 15px;
}

.sticky-top {
  color: white;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.navbarfull {
  transition: all 1s;
  height: 50px;
}

.navbarfull .item-list {
  padding-top: 12.5px;
}

.item-list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  text-align: center;
}

.item-list .nav-item {
  float: left;
}

.item-list-vertical {
  position: absolute;
  right: 0px;
  top: 0px;
  margin: 70px 35px;
  display: block;
  list-style-type: none;
  padding: 10px 20px;
  background-color: #2b2b2bec;
  float: right;
}

.item-list-vertical .nav-item {
  margin: 10px 0;
}


.nav-button {
    float: right;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    background-color: #cc5200;
    font-weight: bold;
    font-size: 40px;
    margin-right: 20px;
  }
.home {
  height: 100vh;
  background-image: url(/home/static/media/zion-home.17b3eab4.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: inset 0 0 5em 1em #000;
}

.home-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.home-content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: rgba(128, 128, 128, 0.432);
  padding: 10px;
  margin: auto;
}

.header-home {
  font-size: 4em;
}

@media screen and (max-width: 500px) {
  .header-home {
    font-size: 2em;
  }
}

.social-links {
  list-style-type: none;
  float: right;
  padding: 0;
}

.social-links > li {
  display: inline-block;
  margin: 10px 15px 0 15px;
  font-size: 2.2em
}

.social-links > li > a {
  color: white;
  float: right;
  transition: all 1s
}

.social-links > li > a:hover {
  color: #2b2b2b;
  float: right;
}


.scroll-button {
    position: absolute;
    margin: 0 0 20px 0;
    bottom: 0;
    height: 80px;
    width: 80px;
    border-radius: 100%;
    background-color: white;
    color: black;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
  }
  
  .scroll-button:hover {
    background-color: rgb(198, 198, 157);
    color: black;
  }

  .bottom.scroll-button:hover {
    padding-top: 30px;
  }

  .top.scroll-button:hover {
    padding-bottom: 30px;
  }




  
.about-container {
    background-color: #2b2b2b;
    min-height: 250px;
    padding-top: 10px;
    padding-bottom: 30px;
  }

  .profile-pic {
    height: 184px;
    align-self: center;
    margin: auto;
    padding-top: 20px;
    margin-right: 20px;
    border-radius: 50%;
  }

  .resume-button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 20px;
  }

  .resume-button {
    background-color: #444;
    padding: 10px;
    color:  #fafbff;
    font-weight: bold;
    transition: all 0.2s ease-in-out;
    font-size: 18px;
  }
  
  .resume-button:hover {
    background-color: #fafbff;
    color: #444;
  }

/* .content-list {
    
} */
.content-item-container {
  margin-bottom: 10px;
  padding-top: 10px;
}

.header {
  margin: 0;
}

.subheader {
  margin-bottom: 3px;
  font-style: italic;
  color: gray;
}

.subtitle {
  margin: 5px 0;
}

.subdescription {
  font-style: italic;
  font-weight: lighter;
  color: gray;
  margin-bottom: 10px;
}


.certifications-container {
  min-height: 500px;
  padding: 25px 0;
  background-color: #fafbff;
}
.footer-container {
  padding: 30px;
  height: 150px;
  background-color: rgb(51, 51, 51);
  color: rgb(186, 212, 216);
  height: 200px;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.links-container {
  margin-bottom: 10px;
}

.links-container > a {
  font-size: 2.5em;
  margin: 0 10px;
  color: white;
  transition: all 1s
}

.links-container > a:hover {
  font-size: 2.5em;
  margin: 0 10px;
  color: #cc5200;
}



.works-header {
  margin-bottom: 30px
}

.works-container {
  min-height: 300px;
}
.portfolio-item {
  width: 100%;
  margin-bottom: 20px;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.25s ease;
  background-color: #2b2b2bf1;
  display: none;
  color: #fafbff;
}

.overlay.hovered {
  display: block;
  opacity: 1;
  /* z-index: 500; */
}

.protfolio-item-container {
  text-align: center;
}

.portfolio-header {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 15px;
}

.portfolio-description {
  text-align: justify;
  font-size: 0.8em;
  max-height: 60%;
  text-overflow: ellipsis;
}

.portfolio-link {
  font-size: 1.5em;
  color: white;
  transition: all 0.5s;
}

.portfolio-link:hover {
  color: rgb(198, 198, 157);
}

/* .link-row {
  margin-top: 0.5em;
  margin: 0 20%
} */

.portfolio-header-small {
  padding-top: 10px;
  font-size: 0.8em;
  font-weight: bold;
}

.link-small {
  font-size: 1em;
}

.link-small.link-row {
  padding: 0 5%;
}

.contact-container {
  padding: 60px 0;
  min-height: 600px;
  background-image: url(/home/static/media/nyc.feaf4051.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  
  position: relative; 


}

.input-container {
  margin: 10px 0;
  width: 100%;
}

input,
textarea {
  font-weight: bold;
  width: 100%;
  background-color: #2b2b2b;
  color: #fafbff;
  border: 1px solid #222222;
  padding: 15px;
}

input:focus,
textarea:focus {
  background-color: rgb(198, 198, 157);
  color: #2b2b2b;
  outline: none;
}

textarea {
  height: 250px;
  resize: none;
}

.submit-button {
  display: inline-block;
  text-align: center;
  color: white;
  font-weight: bold;
  letter-spacing: 3px;
  font-size: 0.75em;
  width: 100%;
  padding: 15px 0;
  background-color: #2b2b2b;
  border: none;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  align-self: flex-end;
  width: 200px;
}

.submit-button:focus,
.submit-button:active,
.submit-button:hover {
  background-color: rgb(198, 198, 157);
  color: #2b2b2b;
  padding-right: 0px;
  padding-left: 30px;
}

.field-label-container {
  text-align: justify;
  font-weight: bold;
  font-size: 1.25em;
  text-align: center;
}

.field-label {
  padding-top: 10px;
  color: #fafbff;
}

.form-container {
  background-color: #2b2b2b4f;
  padding: 10px 2px 10px 2px;
  margin: 60px auto;
  flex-direction: column !important;
}

.validation-error {
  margin-top: 5px;
  margin-bottom: 0;
  color: white;
  font-weight: thin;
  font-size:large;
  height: 20px;
  width: 100%
}

.button-container{
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: center
}

.scroll-top-btn {
  display: flex;
  justify-content: center;
}
.skill-bar-container {
  margin-bottom: 1em;
  width: 100%;
}

.skill-bar-header {
  font-size: 15px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.skill-bar-subtitle {
  font-size: 13px;
  color: gray;
  font-style: italic;
  margin: 0;
  padding: 0;
}
.bar-container {
  display: flex;
  width: 100%;
}

.bar-border {
  align-self: center;
  width: 100%;
  height: 20px;
  border: 2px #2b2b2b solid;
}

.bar-level {
  height:100%;
  background-color: #2b2b2b;
  transition: all 2s cubic-bezier(0.65, 0.05, 0.35, 1.03)
}

skill-bar-container:last-of-type {
  margin-bottom: 30px
}
.skills-list:last-child {
    margin-bottom:30px
}

.resume-container.skills-container {
    min-height: 0;
}
